body {
    padding: 0;
    margin: 0;
    overflow: hidden;
    background-color: rgb(19, 15, 15);
}

* {
    touch-action: pan-y;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

::-webkit-scrollbar {
    display: none;
}